
import HeaderCom from '@/components/common/HeaderCom'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import { mapState, mapGetters, mapActions, mapMutations } from 'vuex'
export default {
name: 'rider',
components: {
XButton,
Group,
Checklist,
HeaderCom,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
beforeRouteEnter (to, from, next) {
next(vm => {
if(vm.accountToken == null) {
vm.$store.dispatch('LOGIN/login').then(res => {
console.log(res)
}).then(res =>{
vm.getPasserList()
})
}else{
vm.getPasserList()
}
})
},
data () {
return {
inlineDescListValue: [],
title: '',
commonList:[]
}
},
computed: {
...mapState('PASSENGER', [
'passengerLis'
]),
...mapState('LOGIN', [
'userInfo'
]),
...mapGetters('LOGIN', [
'accountToken'
])
},
methods: {
getPasserList () {
this.$store.dispatch('PASSENGER/getPassengerList').then(res => {
this.commonList = [];
var Data = res.data;
for (let i = 0; i < Data.length; i++) {
Data[i].key = Data[i].id
Data[i].value = Data[i].name
Data[i].inlineDesc =  Data[i].idCard
}
this.commonList = Data
})
},
...mapMutations("CONTAINER", ["setOptions"]),
choosePassengerId() {
var personList = []
for (let i = 0; i < this.commonList.length; i++) {
for (let j = 0; j < this.inlineDescListValue.length; j++) {
if(this.commonList[i].id == this.inlineDescListValue[j]){
personList.push(this.commonList[i].name)
}
}
}
this.setOptions({
passengerIdList: this.inlineDescListValue,
passengerIdListName: personList.join("")
})
this.$router.push({name: 'index'})
},
changeCheck(val) {
console.log(this.inlineDescListValue)
},
addRider () {
this.$router.push('/addRider');
}
},
mounted() {
console.log(this.accountToken)
},
}



